
<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div class="row">
            <div class="col-md-12 padding-t_8">
                <div class="theme-primary partition-full">
                    <span class="p-name text-white">{{ $t('ScheduleInterview') }}</span>
                    <span class="float-right">
                        <a href="javascript:;" class="p-action-btn text-white" :title="$t('BacktoCandiateProfile')"
                            @click="BackToList">
                            <em class="fa fa-angle-double-left pr-2" aria-hidden="true"></em>
                            <br />{{ $t('BacktoCandiateProfile') }}
                        </a>
                    </span>
                </div>
            </div>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mt-3 p-0">
            <div class="tab-content bod1 col-sm-12 col-md-12 col-lg-9 col-xl-9 float-left p-1 border">
                <div class="float-left col-12 mb-0 evemgmt_details p-2 active show">
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                        <div class="row">
                            <div class="col-sm-12 col-md-12 col-lg-2 col-xl-2 float-left px-1 bg-white">
                                <div class="hr_user-card text-center bg-grey">
                                    <div v-if="profile_path == null  || profile_path == '' ">
                                        <div class="user-image" title="Profile Image">
                                            <img src="https://talygenv.github.io/Vue-UI-Content/images/avatar-icon.jpg"
                                                alt="ProfileImage" />
                                        </div>
                                    </div>
                                    <div v-if="profile_path != null && profile_path != ''  ">
                                        <div class="user-image" title="Profile Image">
                                            <img v-bind:src="profile_path" alt="ProfileImage" />
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="col-sm-12 col-md-12 col-lg-10 col-xl-10 float-left px-1">
                                <div class="hr_user-card p-2 bg-grey" style="min-height:150px;">
                                    <div class="row">
                                        <div class="col-sm-12 col-md-12 col-lg-8 col-xl-8 float-left">
                                            <div v-if="candidateData[0] != undefined"
                                                class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left text-left pt-2">
                                                <div class="form-group">
                                                    <label class="user-label">
                                                        <span class="alpha"><em
                                                                class="alphabet">{{candidateData[0].FIRST_NAME.charAt(0)}}</em></span>
                                                        <small class="text-dark ml-1"> {{$t('Candidate')}} {{$t('Name')}}
                                                        </small>
                                                        <span class="text-primary mt-2">
                                                            {{candidateData[0].FIRST_NAME==null?'---':candidateData[0].FIRST_NAME}}
                                                            <small class="text-dark ml-1">
                                                                <span v-if="candidateData[0].channel_name">
                                                                    {{$t('Channel')}} {{$t('Name')}}:
                                                                    {{candidateData[0].channel_name}}
                                                                </span>
    
                                                                <span
                                                                    v-if="candidateData[0].channel_name && candidateData[0].educations">
                                                                    |
                                                                </span>
                                                                <span v-if="candidateData[0].educations">
                                                                    {{$t('Education_and_Qualification')}}:
                                                                    {{candidateData[0].educations[candidateData[0].educations.length
                                                                    - 1].Degree}}
                                                                </span>
                                                            </small>
                                                        </span>
                                                        <span
                                                            v-if="(candidateData[0].channel_name || candidateData[0].educations)">
                                                            <br />
                                                        </span>
                                                        <span class="cntct mt-1">
                                                            <span v-if="candidateData[0].phone_number">
                                                                <em class="fa fa-mobile pr-1" style="font-size:13pt;"></em>
                                                                <span v-if="candidateData[0].phone_number" class="mr-4">
                                                                    {{$t('PhoneNumber')}}: {{candidateData[0].phone_number}}
                                                                </span>
                                                            </span>
                                                            <em class="fa fa-envelope pr-1"></em>
                                                            <span v-if="candidateData[0].email_id">
                                                                {{$t('Email')}} {{$t('Address')}}:
                                                                {{candidateData[0].email_id}}
                                                            </span>
                                                        </span>
                                                        <span class="mt-1 cntct">
                                                            <span v-if="candidateData[0].talentPoolExperience">
                                                                {{$t('Experience')}}:
                                                                {{candidateData[0].talentPoolExperience[candidateData[0].talentPoolExperience.length
                                                                - 1].occupation}}
                                                            </span>
                                                            <span
                                                                v-if="candidateData[0].talentPoolExperience && candidateData[0].current_location">
                                                                |
                                                            </span>
                                                            <span v-if="candidateData[0].current_location">
                                                                {{$t('Location')}}: <small class="text-dark ml-1 cntct">
                                                                    {{candidateData[0].current_location}}</small>
                                                            </span>
    
                                                        </span>
                                                    </label>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-12 col-lg-4 col-xl-4 float-left text-center">
                                            <a href="javascript:void();"
                                                v-on:click="SubmitSaveScheduleInterview('SCHEDULE')"
                                                class="btn btn-info  mt-5 "><em
                                                    class="fa fa-clock-o pr-2"></em><span>Schedule And Continue</span></a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0 mt-3"
                        v-for="(item,index1) in stagesData" :key="index1">
                        <div class="Search-panel">
                            <div class="search-head search-head_hr">
                                <a class="searchsh-btn_hr" data-toggle="collapse" href="#search-content"
                                    aria-expanded="false" aria-controls="search-content"><span>{{item.STAGE_NAME
                                        }}</span></a>
                                <div class="float-right scinv_cal ml-5">
                                    <label class="float-left mt-1">Interview Date:</label>
                                    <v-date-picker :popover="popover" :minute-increment="5" :masks="masks"
                                            class="input-group" v-model="item.applied_date"
                                            v-bind:name="'date' + item + index1"
                                             v-bind:value="item.applied_date">
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input class="form-control" :value="inputValue" v-on="inputEvents" />
                                                <div @click="togglePopover()" class="input-group-append"><span
                                                        class="input-group-text"><em class="fa fa-calendar"></em></span>
                                                </div>
                                            </template>
                                    </v-date-picker>                                    
                                </div>
                            </div>
                        </div>           
                        <div class="col-12 px-0 float-left mb-2" v-for="(itemR,indexR) in item.Interviewer" :key="indexR">
                            <div class="float-left ml-1 mt-2">
                                <div class="custom-control custom-checkbox">
                                    <input type="checkbox" v-model="itemR.is_selected" class="chkItems custom-control-input"
                                        v-bind:id="'chk_'+index1+indexR">
                                    <label class="custom-control-label universal-custom-control-label pt-1"
                                        v-bind:for="'chk_'+index1+indexR"></label>
                                </div>
                            </div>
                            <div class="float-left hr_name-card ml-3">
                                <div class="form-group">
                                    <div class="user-label">
                                        <span class="alpha"><i class="alphabet">{{itemR.RECRUITER_NAME.charAt(0) }}</i></span>
                                        <div class="text-dark mt-0 float-left d-block scinv_text">
                                            {{itemR.RECRUITER_NAME }}<br>
                                            <small class="text-dark w-100 d-block email">{{itemR.RECRUITER_EMAIL_ID }}</small>
                                            <div v-for="(timezone,index) in item.InterviewerTimeZone" :key="index">
                                                <small class="timezone text-dark d-block" v-for="(userId,indexx) in timezone.USR" v-if="itemR.user_id==userId.user_id" :key="indexx">({{timezone.standard_name!=null? timezone.standard_name:'India Standard Time'}})</small>
                                            </div>
                                        </div>
                                    </div>

                                </div>
                            </div>
                            <div class="float-left timeslots_base">
                                <div v-for="(slot,islot) in timeslots" class="float-left timeslots" :key="islot">
                                    <div class="col" v-bind:class="{'disabled':iseditSlot(slot,item,itemR)==true  || isfilledSlot(slot ,itemR,item) ==true || iseditSlot(slot,item ,itemR).includes('disabled')}" v-on:click="openScheduleInterviewPopup(item ,slot)">
                                        <div class="time">{{slot}}</div>
                                        <div class="timebox" v-if="isfilledSlot(slot,itemR,item) ==false"></div>
                                        <div v-if="isfilledSlot(slot ,itemR,item) ==true" class="timebox red"><i class="fa fa-user-times"></i></div>  
                                        <div v-if="iseditSlot(slot,item,itemR ) != '' && iseditSlot(slot,item,itemR ) != true && isfilledSlot(slot,itemR,item) == false">
                                            <div v-bind:class="{'timeslotsfill':'true' ,'d1':iseditSlot(slot,item ,itemR ).includes('d1')}"></div>
                                            <div v-bind:class="{'timeslotsfill':'true' ,'d2':iseditSlot(slot,item ,itemR ).includes('d2')}"></div>
                                            <div v-bind:class="{'timeslotsfill':'true' ,'d3':iseditSlot(slot,item ,itemR).includes('d3')}"></div>
                                            <div v-bind:class="{'timeslotsfill':'true' ,'d4':iseditSlot(slot,item ,itemR).includes('d4')}"></div>                                       
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="text-center w-100 float-left bg-light py-2">
                                    <a href="javascript:;" v-on:click="openPopUp(item)" class="btn btn_inthr "><em
                                            class="fa fa-plus pr-2"></em><span>Add Interviewer</span></a>
                              </div>
                        </div>
                    </div>
    
                    
                </div>
            </div>
        <div class="col-sm-12 col-md-12 col-lg-3 col-xl-3 float-left p-1">
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mt-2">
                            <h4 v-if="interviewSummaryData.length>0">{{formatDate(interviewSummaryData[0].applied_date)}}</h4>
                            <small class="font_13">Interview Summary</small>
                        </div>
    
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mt-2">
  
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mt-2 bg-grey p-0 hr_borderlightgrey hr_borderltyellow"
                                v-for="(item,index) in interviewSummaryData" :key="index">
                                <div class="row m-0">
                                    <div v-if="item.scheduled_by =='Admin'"
                                        class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left my-2">{{item.FromHours}} ({{
                                        item.timeDifference==null?'N/A':item.timeDifference}})</div>
                                    <div v-if="item.time !='anyTime' &&item.scheduled_by=='Portal' "
                                        class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left my-2">{{item.FromHours}} ({{
                                        item.timeDifference==null?'N/A':item.timeDifference}})</div>
                                    <div v-if="item.time =='anyTime' &&item.scheduled_by=='Portal' "
                                        class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left my-2">Anytime</div>
                                    <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left my-2">{{item.STAGE_NAME}}
                                    </div>
                                </div>
                                <div class="row  m-0">
                                    <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left my-2">
                                        {{item.interview_type}}</div>
                                    <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left my-2"
                                        v-if="item.scheduled_by!=null"><i
                                            class="fa fa-user text-info mr-2"></i>{{item.scheduled_by}}</div>
                                </div>                               
                                <div class="row  m-0" v-if="item.status_name!=null">
                                    <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left my-2">Status</div>
                                    <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left my-2"><i
                                            class="fa fa-check-square-o text-success pr-2"></i>{{item.status_name}}</div>
                                </div>
                            </div>
    

                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mt-2 px-0" v-if="isReschedule">
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mb-1 px-0">New Requested
                                    interview slots by Candidate</div>
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left bg-light p-0 borderblue"
                                    v-for="(itemre,indexx) in rescheduleData" :key="indexx">
                                    <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left my-2">Date:</div>
                                    <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left my-2">
                                        {{itemre.applied_date}}</div>
                                    <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left my-2">Time:</div>
                                    <div v-if="itemre.time !='anyTime'"
                                        class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left my-2">{{itemre.FromHours}} To
                                        {{itemre.ToHours}}</div>
                                    <div v-if="itemre.time =='anyTime'"
                                        class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left my-2">Anytime</div>
                                    <div class="form-group my-2">
                                        <div class="search-btm-btn px-2">
                                            <a href="javascript:;" v-on:click="confirmChanges(itemre)"
                                                class="btn btn-success float-left mr-2 my-1"><i
                                                    class="fa fa-check pr-2"></i><span>OK, Confirmed</span></a>
                                            <a href="javascript:;" v-on:click="GetEmailTemplateByName()"
                                                class="btn btn-danger float-left my-1"><i
                                                    class="fa fa-close pr-2"></i><span>Cancel</span></a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
        
        </div>
        <showscheduleInterviewModal v-if="showscheduleInterviewer" :stages="stagesData" :currentstage="currentstage" @updateStage="updateStageData"></showscheduleInterviewModal>
        <modaladdrecruiter v-if="showAddEditModal" v-bind:currentstage="currentItem" @currentstagee="currentstagee"></modaladdrecruiter>
    </section>
</template>
<script>
import moment from 'moment'
import DataService from '../../../services/DataService';
import showscheduleInterviewModal from '../../../components/Common/Modal/modal/modalschedduleinterview.vue';
import modaladdrecruiter from '../../../components/Common/Modal/modal/modaladdrecruiter.vue'
export default {
    components:{showscheduleInterviewModal,modaladdrecruiter},
  data() {
    return {
      isLoading: false,
      pid: null,
      aid: null,
      candidateData: [],
      interviewSummaryData: [],
      stagesData: [],
      currentstage:[],
      profile_path: '',
      isReschedule: false,
      isScheduled:false,
      UserId:null,
      CompanyId:null,
      currentItem: null,
      showscheduleInterviewer:false,
      showAddEditModal:false,
      EmailContent: '',
      EmailSubject:'',
      starTime: "",
      popover: {
          visibility: 'click'
      },
      masks: {
          input: 'MM-DD-YYYY',
          CompanyType: ''
      },
      Validation:false,
      stage_order:null,
      timeslots: ["12 am", "1 am", "2 am", "3 am", "4 am", "5 am", "6 am", "7 am", "8 am", "9 am", "10 am", "11 am", "12 pm", "1 pm", "2 pm", "3 pm", "4 pm", "5 pm", "6 pm", "7 pm", "8 pm", "9 pm", "10 pm", "11 pm"],

    }
  },
  created: function () {
    var vm = this;
    vm.pid = vm.$route.params.pid;
    vm.aid = vm.$route.params.aid;
    vm.UserId = vm.GetUserInfo.ID;
    vm.CompanyId = vm.GetUserInfo.CompanyId;
    vm.FetchData();
    vm.getScheduledStatus();
  },
  methods: {   
    formatDate: function (value) {        
          if (!value) return '';
            return moment(value).format('MMMM Do, YYYY');          
    },    
    BackToList: function () {
        var vm = this;
        vm.$router.push({ name: 'CandidateDetail', params: { id: vm.aid, JobId: vm.pid } });
    },
    FetchData: function () {
      var vm = this;
      var url = `applicantId=${vm.aid}&departmentIds=0&skillIds=0&langCode=EN&jobID=${vm.pid}`;
      DataService.GetCandidateDetails(url).then(function (response) {
        if (response.data != null) {
          vm.candidateData = response.data.data;
          if (vm.candidateData[0].profile_path != null) {
            vm.BindSrc(vm.candidateData[0].profile_path);
          }
          vm.interviewSummaryData = response.data.stage_data;
          vm.stagesData = response.data.stage_data;
          vm.stagesData = vm.showCurrentList(vm.stagesData.filter(x => x.isCurrent != false));
          for (var i = 0; i < vm.stagesData.length; i++) {
            if (vm.stagesData[i].recruiter_by_stage != null) {
              vm.stagesData[i].Interviewer = vm.stagesData[i].recruiter_by_stage;
            }
          }
          $.each(vm.interviewSummaryData, function (ind, ritem) {
            ritem.timeDifference2 = ritem.timeDifference;
            ritem.timeDifference = vm.timeConvert(ritem.timeDifference);
          });
          $.each(vm.stagesData, function (index, item) {
            $.each(item.Interviewer, function (indexR, itemR) {
              itemR.stage_order = item.STAGE_ORDER;
              itemR.stage_id = item.STAGE_ID;
              itemR.group_id = item.GROUP_ID;
              itemR.job_id = vm.pid;
              itemR.is_selected = true;
              itemR.applicant_id = vm.aid;

            });
            item.duration = 15;
          });

          vm.stagesData.sort(vm.DynamicSort("STAGE_ORDER"))
          if (response.data.STAGE_DATA_RESCHEDULE != null && response.data.STAGE_DATA_RESCHEDULE.length > 0) {
            if (response.data.STAGE_DATA_RESCHEDULE[0].STATUS_CODE == "ReSCHEDULE") {
              vm.rescheduleData = response.data.STAGE_DATA_RESCHEDULE;
              vm.isReschedule = true;
            }
          }
        }
      })
    },
    getScheduledStatus: function () {
      var vm = this;
      var url = `applicantId=${vm.aid}&postId=${vm.pid}`;
      DataService.GetSchIntStatus(url).then(function (response) {
        if (response.data != null) {
              vm.isScheduled = true;
            }
        })
    },
    BindSrc: function (filepath) {
      var path = filepath.substring(0, filepath.lastIndexOf("/") + 1);
      var fileName = filepath.substring(filepath.lastIndexOf("/") + 1, filepath.length);
      let Obj = {
        path: path,
        fileName: fileName
      };
      var url = JSON.stringify(Obj);
      DataService.GetFileSrc(url).then((response) => {
        if (response.data != '') {
          this.profile_path = response.data
        }
      });
    },
    DynamicSort: function (property) {
      var sortOrder = 1;
      if (property[0] === "-") {
        sortOrder = -1;
        property = property.substr(1);
      }
      return function (a, b) {
        var result = (a[property] < b[property]) ? -1 : (a[property] > b[property]) ? 1 : 0;
        return result * sortOrder;
      }

    },
    showCurrentList: function (stagesArray) {
      var vm = this;
      stagesArray.sort((a, b) => new Date(b.created_at).getTime() - new Date(a.created_at).getTime())
      const [head, ...tail] = stagesArray
      const output = tail.reduce(
        (acc, b) => {
          if (!acc.map(o => o.STAGE_ID).includes(b.STAGE_ID)) {
            acc.push(b)
          }
          return acc
        },
        [head]
      )
      return output.sort(vm.DynamicSort('STAGE_ORDER'));
    },
    timeConvert: function (n) {
      if (n == null) {
        var time = "Not yet Scheduled";
        return time;
      }
      var num = n;
      var hours = (num / 60);
      var rhours = Math.floor(hours);
      var minutes = (hours - rhours) * 60;
      var rminutes = Math.round(minutes);
      if (rhours != 0) {
        if (rminutes != 0) {
          return rhours + "hours: " + rminutes + " minutes";
        }
        else {
          return rhours + "hours"
        }
      }
      else {
        return rminutes + " minutes";
      }
    },
    iseditSlot: function(slot, item, interviewer) {        
    var bindTimeclass = false;
    // checkVue.binddisableClass = false;
    if (interviewer.is_selected == false) {
        return "";
    }
    var fromHour = item.FromHours;
    var toHour = item.ToHours;
    var appliedDate = item.applied_date;

    if (appliedDate == null || appliedDate == undefined) {
        return "";
    }

    // Normalize slot format if necessary
    var slot = slot.includes(":") ? slot : slot.replace(" am", ":00 AM").replace(" pm", ":00 PM");
    var dateslot = this.$options.filters.formatDate(appliedDate) + " " + slot;

    if (fromHour != null && fromHour != undefined) {
        fromHour = fromHour.replace("AM", " AM").replace("PM", " PM");
        toHour = toHour.replace("AM", " AM").replace("PM", " PM");
    }

    var datefrom = this.$options.filters.formatDate(appliedDate) + " " + fromHour;
    var date = new Date(datefrom);
    var currentDate = new Date();

    if (currentDate > date) {
        let slotTime = slot.substring(0, slot.indexOf(":"));
        let dateFrom = fromHour.substring(0, fromHour.indexOf(":"));

        if (slotTime == dateFrom) {
            var slotTimePeriod = slot.substring(slot.indexOf(":") + 4);
            var dateFromPeriod = datefrom.substring(datefrom.indexOf(":") + 4);

            if (slotTimePeriod == dateFromPeriod) {
                bindTimeclass = true;
                //checkVue.binddisableClass = true;
            }
        }
    }

    var slottime = new Date(dateslot).getTime();

    if (slottime < new Date().getTime() && bindTimeclass == false) {
        return true;
    }

    if (appliedDate == null || appliedDate == undefined || fromHour == null || fromHour == undefined || toHour == null || toHour == undefined) {
        return "";
    }

    let checkdatefrom = datefrom.replace(datefrom.substring(datefrom.indexOf(":") + 1, datefrom.indexOf(":") + 3), "00");
    var checkfromTime = new Date(checkdatefrom).getTime();
    var fromTime = new Date(datefrom).getTime();
    var toTime = new Date(this.$options.filters.formatDate(appliedDate) + " " + toHour).getTime();

    if (Math.min(checkfromTime, toTime) <= Math.max(slottime, slottime) && Math.max(checkfromTime, toTime) >= Math.min(slottime, slottime)) {
        debugger
        var timeslots = ['00', '15', '30', '45'];
        let change = dateslot.substring(dateslot.indexOf(":") + 1, dateslot.indexOf(":") + 3);
        var classes = [];

        if (bindTimeclass) {
            classes.push('disabled');
        }

        for (const sl of timeslots) {
            var testslot = dateslot.replace(change, sl);
            var testslottime = new Date(testslot).getTime();

            if (!(testslottime >= fromTime && testslottime < toTime)) {
                if (sl == '00') {
                    classes.push('d1');
                } else if (sl == '15') {
                    classes.push('d2');
                } else if (sl == '30') {
                    classes.push('d3');
                } else if (sl == '45') {
                    classes.push('d4');
                }
            }
        }
        return classes;
    }
     else {
        return '';
    }
},           
isfilledSlot: function(slot, interviewer, item) {
    if (interviewer.is_selected == false) {
        return false;
    }

    var vm = this;

    if (slot == null) {
        return false;
    }

    var slotAM = slot.includes(":") ? slot : slot.replace(" am", ":00 AM").replace(" pm", ":00 PM");
    var slotFormatted = slotAM.replace("AM", " AM").replace("PM", " PM");
    var dateslot = this.$options.filters.formatDate(item.applied_date) + " " + slotFormatted;
    var slottime = new Date(dateslot).getTime();

    if (interviewer.filledTimings != undefined && interviewer.filledTimings != null) {
        var filledslotsArr = interviewer.filledTimings.filter(function(filledTiming) {
            var filledTimingFromHours = filledTiming.FromHours.indexOf('M') != 5 && filledTiming.FromHours.indexOf('M') != 6 ? filledTiming.FromHours : filledTiming.FromHours.replace("AM", " AM").replace("PM", " PM");
            var filledTimingToHours = filledTiming.ToHours.indexOf('M') != 5 && filledTiming.ToHours.indexOf('M') != 6 ? filledTiming.ToHours : filledTiming.ToHours.replace("AM", " AM").replace("PM", " PM");

            var filledTimingdatefrom = filledTiming.interview_date + " " + filledTimingFromHours;
            let filledTimingcheckdatefrom = filledTimingdatefrom.replace(filledTimingdatefrom.substring(filledTimingdatefrom.indexOf(":") + 1, filledTimingdatefrom.indexOf(":") + 3), "00");
            var filledTimingcheckfromTime = new Date(filledTimingcheckdatefrom).getTime();

            var filledTimingdateTo = filledTiming.interview_date + " " + filledTimingToHours;
            var filledTimingtoTime = new Date(filledTimingdateTo).getTime();

            if (Math.min(filledTimingcheckfromTime, filledTimingtoTime) <= Math.max(slottime, slottime) && Math.max(filledTimingcheckfromTime, filledTimingtoTime) >= Math.min(slottime, slottime)) {
                return true;
            }
        });

        if (filledslotsArr != null && filledslotsArr.length != 0) {
            for (const filledTiming of filledslotsArr) {
                if (filledTiming.applicant_id != vm.applicant_id || filledTiming.stage_id != item.STAGE_ID) {
                    var filledTimingFromHours = filledTiming.FromHours.indexOf('M') != 5 && filledTiming.FromHours.indexOf('M') != 6 ? filledTiming.FromHours : filledTiming.FromHours.replace("AM", " AM").replace("PM", " PM");
                    var filledTimingToHours = filledTiming.ToHours.indexOf('M') != 5 && filledTiming.ToHours.indexOf('M') != 6 ? filledTiming.ToHours : filledTiming.ToHours.replace("AM", " AM").replace("PM", " PM");

                    var filleddatefrom = filledTiming.interview_date + " " + filledTimingFromHours;
                    let filledcheckdatefrom = filleddatefrom.replace(filleddatefrom.substring(filleddatefrom.indexOf(":") + 1, filleddatefrom.indexOf(":") + 3), "00");
                    var filledcheckfromTime = new Date(filledcheckdatefrom).getTime();
                    var filledtoTime = new Date(filledTiming.interview_date + " " + filledTimingToHours).getTime();

                    if (Math.min(filledcheckfromTime, filledtoTime) <= Math.max(slottime, slottime) && Math.max(filledcheckfromTime, filledtoTime) > Math.min(slottime, slottime)) {
                        return true;
                    }
                }
            }
        }

        return false;
    }

    return false;
},
  
    openPopUp: function (item) {
        debugger
        this.currentItem = item
        this.showAddEditModal = true;
    },
    openScheduleInterviewPopup:function (item, starttime,index) { 
        if (item.applied_date == null || item.applied_date == undefined) {            
            this.ShowAlert(this.$t("Pleasechooseinterviewdate"), "warning", true, this.$t("Alert"));
            return false
        }
        this.currentstage = item;
        this.showscheduleInterviewer = true;
        this.starTime = starttime.replace(" am", ":00 AM").replace(" pm", ":00 PM");
    },
    checkValidation: function (validationData){            
            var vm=this;
            vm.Validation = true;
            if (validationData == null || validationData =='')
            {            
                vm.ShowAlert(vm.$t("StageRoundValidation"), "warning", true, vm.$t("Alert"));
                return vm.Validation = false;
            }
            vm.stage_order = validationData[0].STAGE_ORDER;
            let tempIntervieDate = validationData[0].applied_date;
            let tempFromHours = validationData[0].FromHours;
            let tempToHours = validationData[0].ToHours;
            if (vm.stage_order != 1)
            {                
                vm.ShowAlert(vm.$t("StageRoundValidation"), "warning", true, vm.$t("Alert"));
                return vm.Validation = false;
            }
            for (let i = 0; i < validationData.length; i++)
            {
                if (i > 0)
                {
                    var diffstageorder = validationData[i].STAGE_ORDER - vm.stage_order;
                    if (diffstageorder == 1)
                    {
                        vm.stage_order = validationData[i].STAGE_ORDER;
                    }
                    else
                    {
                        vm.ShowAlert(vm.$t("StageRoundValidation"), "warning", true, vm.$t("Alert"));
                        return vm.Validation = false;
                    }
                    debugger
                    tempFromHours = tempFromHours.includes("AM") ? tempFromHours.replace("AM", " AM") : tempFromHours;
                    tempFromHours = tempFromHours.includes("PM") ? tempFromHours.replace("PM", " PM") : tempFromHours;
                    tempToHours = tempToHours.includes("AM") ? tempToHours.replace("AM", " AM") : tempToHours;
                    tempToHours = tempToHours.includes("PM") ? tempToHours.replace("PM", " PM") : tempToHours;
                    validationData[i].FromHours = validationData[i].FromHours.includes("AM") ? validationData[i].FromHours.replace("AM", " AM") : validationData[i].FromHours;
                    validationData[i].FromHours = validationData[i].FromHours.includes("PM") ? validationData[i].FromHours.replace("PM", " PM") : validationData[i].FromHours;
                    validationData[i].ToHours = validationData[i].ToHours.includes("AM") ? validationData[i].ToHours.replace("AM", " AM") : validationData[i].ToHours;
                    validationData[i].ToHours = validationData[i].ToHours.includes("PM") ? validationData[i].ToHours.replace("PM", " PM") : validationData[i].ToHours;
                    tempFromHours = new Date(this.$options.filters.formatDate(tempIntervieDate) + ' ' + tempFromHours);
                    tempToHours = new Date(this.$options.filters.formatDate(tempIntervieDate) + ' ' + tempToHours);
                    var TempFromTime = new Date(validationData[i].applied_date + ' ' + validationData[i].FromHours);
                    var TempToTime = new Date(validationData[i].applied_date + ' ' + validationData[i].ToHours);
                    if (Date.parse(this.$options.filters.formatDate(tempIntervieDate)) > Date.parse(validationData[i].applied_date))
                    {                       
                        vm.ShowAlert(vm.$t("InterViewRoundsValidation"), "warning", true, vm.$t("Alert"));
                        return vm.Validation = false;
                    }
                    if (Date.parse(this.$options.filters.formatDate(tempIntervieDate)) == Date.parse(validationData[i].applied_date))
                    {
                        if (Date.parse(TempFromTime) <= Date.parse(tempFromHours)) {
                           vm.ShowAlert(vm.$t("InterViewRoundsValidation"), "warning", true, vm.$t("Alert"));
                            return vm.Validation = false;
                        }
                        if (Date.parse(TempFromTime) >= Date.parse(tempFromHours) && Date.parse(TempFromTime) <= Date.parse(tempToHours)) {
                            vm.ShowAlert(vm.$t("InterViewRoundsValidation"), "warning", true, vm.$t("Alert"));
                            return vm.Validation = false;
                        }
                        if (Date.parse(TempToTime) >= Date.parse(tempFromHours) && Date.parse(TempToTime) <= Date.parse(tempToHours)) {
                            vm.ShowAlert(vm.$t("InterViewRoundsValidation"), "warning", true, vm.$t("Alert"));
                            return vm.Validation = false;
                        }
                        else
                        {
                            tempIntervieDate = validationData[i].applied_date;
                            tempFromHours = validationData[i].FromHours;
                            tempToHours = validationData[i].ToHours;
                        }
                    }
                    else
                    {
                        tempIntervieDate = validationData[i].applied_date;
                        tempFromHours = validationData[i].FromHours;
                        tempToHours = validationData[i].ToHours;
                    }
                }
            }
            return vm.Validation;
    },
    updateStageData : function(updatedStage) { 
      const index = this.stagesData.findIndex(stage => stage.STAGE_ID === updatedStage.STAGE_ID);
      if (index !== -1) {
        this.$set(this.stagesData, index, updatedStage);
      }
    },
    currentstagee:function(currentstagee){      
      this.currentstage=currentstagee;
    },
    confirmChanges: function (item) {
                var vm = this;
                var rescheduledStage = vm.stagesData.find(x => x.STAGE_ID == item.STAGE_ID);
                if (rescheduledStage != null) {
                    item.FromHours = item.FromHours.replace("AM", " AM").replace("PM", " PM");
                    item.ToHours = item.ToHours.replace("AM", " AM").replace("PM", " PM");
                    var rescheduledFromDate = item.applied_date
                    var DaterescheduledFromDate = new Date(rescheduledFromDate)
                    var rescheduled_date = "".concat(DaterescheduledFromDate.getMonth() + 1, "/", DaterescheduledFromDate.getDate(), "/", DaterescheduledFromDate.getFullYear())
                    if (item.time == "anyTime") {
                        if (DaterescheduledFromDate < new Date().getTime()) {                            
                            vm.ShowAlert(vm.$t("Dates that have passed cannot be rescheduled."), "warning", true, vm.$t("Alert"));
                            return false;
                        }
                        rescheduledStage.applied_date = rescheduled_date;
                        rescheduledStage.FromHours = '';
                        rescheduledStage.ToHours = '';
                        ShowAlert(vm.$t("Please select time for") + item.STAGE_NAME + "stage", "warning", true, vm.$t("Alert"));
                        rescheduledStage.status_code = 'CONFIRM';
                        return true;
                    }
                    else{
                        rescheduledFromDate += " " +item.FromHours;
                        var rescheduledFromTime = new Date(rescheduledFromDate).getTime();
                        if (rescheduledFromTime < new Date().getTime()) {
                          vm.ShowAlert(vm.$t("Dates that have passed cannot be rescheduled."), "warning", true, vm.$t("Alert"));
                            return false;
                        }
                        for (const interviewer of rescheduledStage.Interviewer) {
                            if (vm.isfilledSlot(item.FromHours, interviewer, item)) {                               
                                vm.ShowAlert(vm.$t("During this time period, interviewers are busy."), "warning", true, vm.$t("Alert"));
                                return false;
                            }
                        }
                        rescheduledStage.applied_date = rescheduled_date;
                        rescheduledStage.FromHours = item.FromHours;
                        rescheduledStage.ToHours = item.ToHours;
                        rescheduledStage.status_code = 'CONFIRM';                       
                        vm.SubmitSaveScheduleInterview('SCHEDULE');
                        return true;
                    }
                }
            },
            GetEmailTemplateByName() {
                var vm = this;
                var url = 'templateName=HiringCancelInterviewSchedule';
                  DataService.GetEmailTemplateID(url)
                    .then(response => {
                        vm.EmailContent = response.data.DATA[0].template_content;
                        vm.EmailSubject = response.data.DATA[0].template_subject;
                        vm.CancelInterviewSlot();                        
                    })
            },
    SubmitSaveScheduleInterview: function (status_code) { 
        //debugger     
        var selectedInterviewers = [];
        var vm = this;
        var isRecord = false;
        var recuiterData = [];
        vm.validateArray = [];
        for (const stage of  vm.stagesData) {
            if (stage.FromHours != null && stage.ToHours != null && stage.applied_date != null){
                var obj = {
                    'applicant_id': vm.aid,
                    'job_id': vm.pid,
                    'stage_order': stage.STAGE_ORDER,
                    'stage_id': stage.STAGE_ID,
                    'interview_date': stage.applied_date,
                    'interview_from_time': stage.FromHours,
                    'interview_to_time': stage.ToHours,
                    'group_id': stage.GROUP_ID,
                    'scheduled_by': 'Admin',
                    'company_id': vm.CompanyId,
                    'created_by': vm.UserId,
                    'status_id': 1001,
                    'status_code': stage.status_code == "CONFIRM" ? stage.status_code : status_code,
                    'EmailTo': vm.candidateData[0].email_id,
                    'EmailSubject': '',
                    'place': stage.place,
                    'interViewType': stage.interview_type,
                    'isScheduled': vm.isScheduled
                }  
               // debugger
                var fromDttime = vm.$options.filters.formatDate(obj.interview_date) + ' ' + obj.interview_from_time;
                var toDttime = vm.$options.filters.formatDate(obj.interview_date) + ' ' + obj.interview_to_time;
                var fDatetime = fromDttime;
                var tDatetime = toDttime;
                if (Date.parse(fDatetime) > Date.parse(tDatetime)) {                   
                    vm.ShowAlert(vm.$t("FromToTimeRangeValidation"), "warning", true, vm.$t("Alert"));
                    return false;
                }
                recuiterData.push(obj);  
                for (const interviewer of stage.Interviewer) {
                    if (interviewer.is_selected) {
                        selectedInterviewers.push(interviewer);
                    }
                }
                vm.validateArray.push(stage);                   
            }
            if (stage.applied_date != null && stage.FromHours != null && stage.ToHours != null) {
                isRecord = true;
            }            
        }
        if (recuiterData == null || recuiterData == ''){            
            vm.ShowAlert(vm.$t("RecordsEnter", [vm.$t("PleaseSelectInterview")]), "failure", true, vm.$t("Alert"));
            return false;
        }
        if (!vm.checkValidation(vm.validateArray)){
            return false;
        }
        if (!isRecord) {
            vm.ShowAlert(vm.$t("RecordsEnter", [vm.$t("AtLeast")]), "failure", true, vm.$t("Alert"));
            return;
        }
        var postJSON = {
            data: recuiterData,
            selectedInterviewers: selectedInterviewers
        };
        var objectPostString = JSON.stringify(postJSON);
        DataService.SaveScheduleInterview(objectPostString).then(function (response) {
            if(response.data.status == "Success") {
                if (response.data.code == 0) {
                    // talygen.alert("Group Already Registered")
                }else{
                    vm.FetchData();
                    if (status_code == 'SCHEDULE') {                        
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("SchedulerAdded")]), "success", true, vm.$t("Alert"));
                       // vm.Message = "";                       
                        //setTimeout(function () { window.location.href = '/hr/interviewschedulenotification?id=@Context.Request.Query["aid"]&pid=@Context.Request.Query["pid"]&isd=' + vm.isScheduled; }, 1000);
                        vm.stagesData = [];
                    }
                }
            }
         });

    }

  }
}
</script>
