<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('AddInterviewer') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>                        
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../../../services/DataService';
export default {
    props: {
        currentstage: {
            type: Object,
            required: true
        },
    },
    data(){
        return{
            isLoading:false,
            ddlRecruiter: [],
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "CustomSelectField",
                            mode: "tag",
                            label: this.$t('Recruiters'),
                            name: "Recruiters",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select Recruiters'),
                            validationRules: { "required": true, max: 100 },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",                   
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ],
        }
    },
    created: function () {
        this.GetRecruiters();
    },
    methods:{
        GetRecruiters:async function(){
            var vm=this;
            var params=`moduleName=Hiring`;
            await DataService.GetRecruiterList(params).then((response) => {                
                vm.isLoading = false;
                if(response.data.DATA.length>0){                    
                    vm.FormSchema[0].Data[0].config.options=response.data.DATA;
                }
            })
        },
        Close:function(){
            this.$parent.showAddEditModal=false;
        },
        async onSubmit(formfields, val) {            
            var vm=this;
            vm.ddlRecruiter=formfields.Recruiters;
             for (const recruiter of vm.ddlRecruiter) {
                if (vm.currentstage.Interviewer != null && vm.currentstage.Interviewer != '') {
                    if (vm.currentstage.Interviewer.find(x => x.user_id == recruiter.value) == undefined) {
                       // debugger;
                         var params=`interviewer_id=${recruiter.value}`;
                            await DataService.GetRecruiterFilledTimeSLots(params).then((response) => {  
                              var json = response.data;                              
                                var currentstage = vm.$parent.stagesData.find(x => x.STAGE_ID == vm.currentstage.STAGE_ID)
                                var interviewer = {
                                    'stage_order': vm.currentstage.STAGE_ORDER,
                                    'RECRUITER_EMAIL_ID': json.interviewerData[0].email_id,
                                    'RECRUITER_NAME': json.interviewerData[0].first_name,
                                    'user_id': recruiter.value,
                                    'stage_id': vm.currentstage.STAGE_ID,
                                    'group_id': vm.currentstage.GROUP_ID,
                                    'job_id':vm.$parent.pid,
                                    'applicant_id':vm.$parent.aid,
                                    'is_selected': true,
                                    'filledTimings': []
                                }
                                if (json != null && json.interviewerData[0].filledTimings != undefined) {
                                    interviewer.filledTimings = json.interviewerData[0].filledTimings;
                                }
                                if (vm.$parent.isfilledSlot(currentstage.FromHours, interviewer, currentstage)) {                                    
                                    vm.ShowAlert(vm.$t("Someoftheinterviewersaddedare"), "warning", true, vm.$t("Alert"));
                                    return false;
                                }
                                if (vm.$parent.isfilledSlot(currentstage.ToHours, interviewer, currentstage)) {                                   
                                    vm.ShowAlert(vm.$t("Someoftheinterviewersaddedare"), "warning", true, vm.$t("Alert"));
                                    return false;
                                }
                                currentstage.Interviewer.push(interviewer);
                                vm.$emit('currentstagee', stage);
                        })
                    }
                }
                vm.Close();
             }
        }       

    }
}
</script>