<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('ScheduleInterview') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>                        
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import moment from 'moment';
export default {
    props: {
        stages: {
            type: Array,
            required: true
        },
        currentstage: {
            type: Object,
            required: true
        },
    },
    data(){
        return{
            isLoading:false,
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                    {
                            astype: "SelectField",
                            label: this.$t('Stage'),
                            name: "Stage",
                            value: "",
                            placeholder: this.$t('Stage'),
                            validationRules: {
                                "required": false
                            },
                            config:{
                                options: [],
                            },
                            disabled:true                         
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                    {
                            astype: "SelectField",
                            label: this.$t('InterviewType'),
                            name: "InterviewType",
                            value: "On Site",
                            placeholder: this.$t('InterviewType'),
                            validationRules: {
                                "required": false
                            } ,
                            config:{
                                options: [{name:'On Site', value:'On Site'},{name:'Telephone', value:'Telephone'},{name:'Web Conference', value:'Web Conference'}],
                            }                           
                        },
                    ]
                },
                {
                    layoutType: "double",
                    Data: [
                    {
                            astype: "DateField",
                            label: this.$t('EventStartTime'),
                            name: "EventStartTime",
                            value: "",
                            placeholder: this.$t('EventStartTime'),
                             config: {
                                mode: "Time",                                
                                format: "hh:mm"
                            },
                            validationRules: {
                                "required": false
                            }                          
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Duration'),
                            name: "Duration",
                            value: "15",
                            placeholder: this.$t('Duration'),
                            validationRules: {
                                "required": false
                            },
                            config:{
                                options: [{ name: "15 Mins", value: 15 }, { name: "30 Mins", value: 30 }, { name: "45 Mins", value: 45 }, { name: "1 hr", value: 60 }, { name: "1:15 Mins", value: 75 }, { name: "1:30 Mins", value: 90 }, { name: "1:45 Mins", value: 105 }, { name: "2hrs", value: 120 }],
                            }                                                    
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                    {
                            astype: "TextField",
                            label: this.$t('Place'),
                            name: "Place",
                            value: "",
                            placeholder: this.$t('Place'),
                            validationRules: {
                                "required": false
                            } ,
                            config:{
                                options: [],
                            }                           
                        },
                    ]
                }
            ],
            buttons: [
               {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible:true,
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-send pr-2'></i>Update",
                    isVisible:false                    
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ],
            isedit: false,
            starttime:''
        }
    },
    created:function(){        
        var vm=this;
        vm.bindStage();         
        vm.FormSchema[2].Data[0].value = vm.$parent.starTime;      
        if (vm.currentstage.FromHours != null && vm.currentstage.ToHours && vm.currentstage.applied_date != null) {   
            vm.FormSchema[2].Data[0].value=vm.currentstage.applied_date;         
            vm.currentstage.FromHours = (vm.currentstage.FromHours.indexOf('M') != 5 && vm.currentstage.FromHours.indexOf('M') != 6)? vm.currentstage.FromHours  :vm.currentstage.FromHours.replace("AM", " AM").replace("PM", " PM");
            vm.currentstage.ToHours = (vm.currentstage.ToHours.indexOf('M') != 5 && vm.currentstage.ToHours.indexOf('M') != 6) ? vm.currentstage.ToHours : vm.currentstage.ToHours.replace("AM", " AM").replace("PM", " PM");
            var datefrom = vm.$options.filters.formatDate(vm.currentstage.applied_date) + " " + vm.currentstage.FromHours
            let checkdatefrom = datefrom.replace(datefrom.substring(datefrom.indexOf(":") + 1, datefrom.indexOf(":") + 3), "00");
            var slotdate = vm.$options.filters.formatDate(vm.currentstage.applied_date) + " " + vm.$parent.starTime;
            var dateTo = vm.$options.filters.formatDate(vm.currentstage.applied_date) + " " + vm.currentstage.ToHours;         
            var checkfromTime = new Date(checkdatefrom).getTime();
            var toTime = new Date(dateTo).getTime();
            var slottime = new Date(slotdate).getTime();            
            if (Math.min(checkfromTime, toTime) <= Math.max(slottime, slottime) && Math.max(checkfromTime, toTime) > Math.min(slottime, slottime)) {
                vm.FormSchema[1].Data[0].value = vm.currentstage.interview_type;
                vm.FormSchema[2].Data[1].value = vm.currentstage.timeDifference2;
                vm.FormSchema[3].Data[0].value = vm.currentstage.place;
                vm.FormSchema[2].Data[0].value = vm.currentstage.FromHours;
                vm.isedit = true;
            }
            else {
                vm.FormSchema[2].Data[0].value = vm.$parent.starTime;
                vm.FormSchema[2].Data[1].value = vm.currentstage.timeDifference2;
                vm.isedit = false;
            }
        }
        else {            
            vm.FormSchema[2].Data[0].value = vm.$parent.starTime;
            vm.isedit = false;
        }
        if(vm.isedit){ 
            vm.buttons[0].isVisible=false;
            vm.buttons[1].isVisible=true;
        }
        else{
            vm.buttons[0].isVisible=true;
            vm.buttons[1].isVisible=false;
        }
    },
    methods:{
        bindStage:function(){            
            var vm=this;
            vm.stages.forEach((item,index)=>{ 
                if(item.STAGE_ID==vm.currentstage.STAGE_ID){                        
                    vm.FormSchema[0].Data[0].value=item.STAGE_ID;
                }                 
                let obj={
                    value:item.STAGE_ID,
                    name:item.STAGE_NAME,
                }
                vm.FormSchema[0].Data[0].config.options.push(obj); 
            })
        },
        Close:function(){
            this.$parent.showscheduleInterviewer=false;
        },
        calculateEndTime:function(starttime, duration){
            //debugger            
            if (!starttime || !duration) return '';
                //let startTimeMoment = moment("1970-01-01" + starttime, "YYYY-MM-DD hh:mm A");
                //let endTimeMoment = startTimeMoment.add(duration, 'minutes');
                let endTimeMoment = moment(starttime, 'h:mm A').add(duration, 'minutes');
                return endTimeMoment.format('h:mm A');
                //return endTimeMoment.format('HH:mm');
        },
        calcluateToHours: function (time) {
            time = time.toString().match(/^([01]\d|2[0-3])(:)([0-5]\d)(:[0-5]\d)?$/) || [time];
            if (time.length > 1) { 
                time = time.slice(1);  
                time[5] = +time[0] < 12 ? ' AM' : ' PM'; 
                time[0] = +time[0] % 12 || 12; 
            }
            return time.join('');
        },
        onSubmit(formfields) {       
            //debugger      
            var vm=this;
            var stage = vm.$parent.stagesData.find(x => x.STAGE_ID == vm.currentstage.STAGE_ID);
            let filledTimingcheckdatefrom = moment(formfields.EventStartTime).startOf('hour').format('YYYY-MM-DD HH:00:00');
            let appledi_datetime = vm.$options.filters.formatDate(stage.applied_date) + " " + filledTimingcheckdatefrom;
            var toHours =  vm.calculateEndTime(vm.$options.filters.formatTimee(formfields.EventStartTime),formfields.Duration);
            var tohoursC = vm.calcluateToHours(toHours)
            let till_dateTime = stage.applied_date + " " + tohoursC;
            var busyTiming = [];
            $.each(stage.Interviewer, function (index, datetime) {
                var stageDate = moment(new Date(stage.applied_date), "YYYY-MM-DD");
                stageDate = stageDate.format('YYYY-MM-DD');
                let timingExists = stage.Interviewer[index];
                timingExists = timingExists.hasOwnProperty('filledTimings');
                 if (timingExists){
                     if (stage.Interviewer[index].filledTimings.length > 0) {
                         var stageDataTime = stage.Interviewer[index].filledTimings.filter(x => x.interview_date == stageDate);  
                         if (stageDataTime.length > 0 && vm.$parent.aid != stageDataTime[0].applicant_id){
                            $.each(stageDataTime, function (index, date) {                                      
                                var FromHours = stageDataTime[index].FromHours;
                                var ToHours = stageDataTime[index].ToHours;
                                var applied_date = stageDataTime[index].interview_date;
                                var obj ={
                                    FromHours: FromHours,
                                    ToHours: ToHours,
                                    applied_date: applied_date
                                };
                                busyTiming.push(obj);
                            });
                         }
                     }
                }
            });
            if (busyTiming.length > 0) {
                for (let index = 0; index < busyTiming.length; index++) {
                    let tempFromHours = "";
                    let tempToHours = "";
                    tempFromHours = busyTiming[index].FromHours.includes("PM") ? busyTiming[index].FromHours.replace("PM", " PM") : busyTiming[index].FromHours;
                    tempFromHours = tempFromHours.includes("AM") ? tempFromHours.replace("AM", " AM") : tempFromHours;
                    tempToHours = busyTiming[index].ToHours.includes("PM") ? busyTiming[index].ToHours.replace("PM", " PM") : busyTiming[index].ToHours;
                    tempToHours = tempToHours ? tempToHours.replace("AM", " AM") : tempToHours;
                    var fromDate = new Date(busyTiming[index].applied_date + " " + tempFromHours);
                    var toDate = new Date(busyTiming[index].applied_date + " " + tempToHours);
                    if (Date.parse(fromDate) > Date.parse(appledi_datetime) && Date.parse(fromDate) < Date.parse(till_dateTime)) {
                        vm.ShowAlert(vm.$t("Pleasechooseinterviewdate"), "warning", true, vm.$t("Alert"));
                        return false;
                    }
                    if (Date.parse(toDate) > Date.parse(appledi_datetime) && Date.parse(toDate) < Date.parse(till_dateTime)) {
                        vm.ShowAlert(vm.$t("Pleasechooseinterviewdate"), "warning", true, vm.$t("Alert"));
                        return false;
                    }

                }
            }            
            if (new Date(appledi_datetime).getTime() < new Date().getTime()) {
                vm.ShowAlert(vm.$t("Datesthathavepassed"), "warning", true, vm.$t("Alert"));
                return false;
            }
            stage.FromHours = vm.$options.filters.formatTimee(formfields.EventStartTime)
            stage.ToHours = tohoursC;
            stage.interview_type = formfields.InterviewType;
            stage.place = formfields.Place;
            stage.timeDifference2 = formfields.Duration;
            vm.$emit('updateStage', stage);          
            vm.Close();
        }
    }
}
</script>